import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../shared/authentication-service'
import {GlobalService} from '../global.service';
import {NonAdmins} from '../non-admins'
import { ToastController } from '@ionic/angular';
import { MenuController } from '@ionic/angular';
import { Router } from "@angular/router";
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.page.html',
  styleUrls: ['./dashboard.page.scss'],
  
})
export class DashboardPage implements OnInit {
  userArray:any = [{
    id: "",
    data: {} as NonAdmins
   }];

  constructor( private router : Router,private menu: MenuController,public authService: AuthenticationService, public global: GlobalService,public toastController: ToastController) { 

  //this.getUserList();
  }

  openFirst() {
    this.menu.enable(true, 'first');
    this.menu.open('first');
  }

  openEnd() {
    this.menu.open('end');
  }

  openCustom() {
    this.menu.enable(true, 'custom');
    this.menu.open('custom');
  }
  getUserList(){
    this.global.consult("tareas").subscribe((resultadoConsultaTareas) => {
      this.userArray = [];
      resultadoConsultaTareas.forEach((userData: any) => {
        this.userArray.push({
          id: userData.payload.doc.id,
          data: userData.payload.doc.data()
        });
      })
    });
  }
  dataArray;

  ngOnInit() {
    this.dataArray =JSON.parse(localStorage.getItem('user'));
  }

  signOut(){
    localStorage.removeItem('user');
    localStorage.setItem('loggedIn','0');
    this.authService.SignOut();
  }
  
}


